		color:#aaaaaa;
	}
	.zixun .center{
		width: 100%;
		height: auto;
		padding: 40px;
		background: #000000;
		float: left;
	}
	.zixun .center_c{
		max-width: 1200px;
		height: auto;
		margin: 0 auto;
		float: left;
	}
	.zixun .center_c li{
		width: 30%;
		height: 695px;
		background: #262626 none repeat center center;
		float: left;
		margin: 0 1.5% 20px;
	}
	.zixun .center_c li img{
		width: 100%;
		height: auto;
	}
	.zixun .center_c .b{
		width: 100%;
		height: auto;
    	position: relative;
	}
